const PACKAGE_NAME = '@wix/auto_sdk_media_folders';

/**
 * Creates a new folder in the Media Manager. <br/>
 *
 * Use the `parentFolderId` parameter to specify in which existing folder you want the new folder to be created.
 * If no folder is specified, the new folder is created in the `media-root` folder.
 */
export function createFolder(payload: object): RequestOptionsFactory<any> {
  function __createFolder({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.media.site_media.v1.folder',
      method: 'POST' as any,
      methodFqn: 'com.wix.media.site_media.v1.FoldersService.CreateFolder',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveComWixMediaSiteMediaV1FoldersServiceUrl({
        protoPath: '/v1/folders',
        data: payload,
        host,
      }),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'folder.createdDate' },
              { path: 'folder.updatedDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __createFolder;
}

